/**
 * The following styles get applied both on the front of your site
 * and in the editor.
 *
 * Replace them with your own styles or remove the file completely.
 */

.wp-block-woocommerce-events-fooevents-event-attendees {
}

/*  Attendee Block Grid Styles */

.fooevents-attendees-grid {
    display: grid;
    gap: 30px;
}

.fooevents-attendee-list-grid-2 {
    grid-template-columns: repeat(2, calc(50% - (1 * 30px) / 2));
}

.fooevents-attendee-list-grid-3 {
    grid-template-columns: repeat(3, calc(33.33% - (2 * 30px) / 3));
}

.fooevents-attendee-list-grid-4 {
    grid-template-columns: repeat(4, calc(25% - (3 * 30px) / 4));
}

.fooevents-attendee-list-grid-5 {
    grid-template-columns: repeat(5, calc(20% - (4 * 30px) / 5));
}

.fooevents-attendee-list-grid-6 {
    grid-template-columns: repeat(6, calc(16.66% - (5 * 30px) / 6));
}

.fooevents-attendee-list-grid-7 {
    grid-template-columns: repeat(7, calc(14.28% - (6 * 30px) / 7));
}

.fooevents-attendee-list-grid-8 {
    grid-template-columns: repeat(8, calc(12.5% - (7 * 30px) / 8));
}

.fooevents-attendees-grid .fooevents-attendee {
    margin-bottom:1em;
    text-align: center;
    line-height: 1.3em;
}

.fooevents-attendees:after,
.fooevents-attendee-list-clear {
    clear: both;
    content: '';
    display: block;
}

.fooevents-attendees-grid .fooevents-attendee img {
    border-radius: 100%;
    max-width: 90%;
    margin: 0 0 .5em;
    display: inline-block;
}

.fooevents-attendees-grid .fooevents-attendee-info-name {
    font-weight: bold;
}

.fooevents-attendees-grid .fooevents-attendee-info-designation,
.fooevents-attendees-grid .fooevents-attendee-info-company,
.fooevents-attendees-grid .fooevents-attendee-info-telephone,
.fooevents-attendees-grid .fooevents-attendee-info-email,
.fooevents-attendees-grid .fooevents-attendee-info-signup {
    font-size:.8em;
}
.fooevents-attendees-grid .fooevents-attendee-info-designation {
    font-weight: 600;
}

.fooevents-attendees-grid-total {
    padding:1em 0;
    text-align: center;
}

/* Attendee Block Name Styles */
.fooevents-attendee strong {
    font-size: .9em;
    font-weight: 600;
}

/* Attendee Block Pictures Styles */
.fooevents-attendees-pictures .fooevents-attendee {
    float: left;
    text-align: center;
    line-height: 1.3em;
    margin: 0 0 0 -.8em;
}
  
.fooevents-attendees-pictures .fooevents-attendee img {
    max-width:100%;
}

.fooevents-attendees-pictures .fooevents-attendee-size-large {
    width: 6em;
}

.fooevents-attendees-pictures .fooevents-attendee-size-large img {
    border: solid 4px #fff;
    width: 6em;
}

.fooevents-attendees-pictures .fooevents-attendee-size-medium {
    width: 3.5em;
}

.fooevents-attendees-pictures .fooevents-attendee-size-medium img {
    border: solid 3px #fff;
    width: 3.5em;
}

.fooevents-attendees-pictures .fooevents-attendee-size-small {
    width: 2.5em;
}

.fooevents-attendees-pictures .fooevents-attendee-size-small img {
    border: solid 2px #fff;
    width: 2.5em;
}

#fooevents-attendee-list-compact .fooevents-attendee-large img {
    max-width:6em;
}

#fooevents-attendee-list-compact .fooevents-attendee-medium img {
    max-width:3.5em;
}

#fooevents-attendee-list-compact .fooevents-attendee-small img {
    max-width:2.5em;
}

.fooevents-attendees-pictures .fooevents-attendee-0 {
    margin:0;
}

/* Attendee Block List Styles */
.fooevents-attendees-list img {
    border-radius: 100%;
    max-width: 90%;
    margin: 0 0 .5em;
}

.fooevents-attendees-list-total {
    padding:1em 0;
    text-align: left;
}    

/* Attendee Block Image Styles */
.fooevents-attendees-pictures .fooevents-attendee img {
    border-radius: 100%;
    max-width: 100%;
    margin: 0 0 .5em;
}

/* Responsive Styles */
@media screen and (max-width: 55em) {

    .fooevents-attendee-list-grid-5,
    .fooevents-attendee-list-grid-6,
    .fooevents-attendee-list-grid-7,
    .fooevents-attendee-list-grid-8 {
        grid-template-columns: repeat(5, calc(20% - (4 * 30px) / 5));
    }    

}

@media screen and (max-width: 50em) {

    .fooevents-attendee-list-grid-5,
    .fooevents-attendee-list-grid-6,
    .fooevents-attendee-list-grid-7,
    .fooevents-attendee-list-grid-8 {
        grid-template-columns: repeat(4, calc(25% - (3 * 30px) / 4));
    }  
}

@media screen and (max-width: 40em) {

    .fooevents-attendee-list-grid-4,
    .fooevents-attendee-list-grid-5,
    .fooevents-attendee-list-grid-6,
    .fooevents-attendee-list-grid-7,
    .fooevents-attendee-list-grid-8 {
        grid-template-columns: repeat(3, calc(33.33% - (2 * 30px) / 3));
    }  

    #fooevents-attendee-list-compact th {
        display: none;
    }
    
    #fooevents-attendee-list-compact td {
        display: block;
        width: 100%;
    }
}

@media screen and (max-width: 30em) {

    .fooevents-attendee-list-grid-3,
    .fooevents-attendee-list-grid-4,
    .fooevents-attendee-list-grid-5,
    .fooevents-attendee-list-grid-6,
    .fooevents-attendee-list-grid-7,
    .fooevents-attendee-list-grid-8 {
        grid-template-columns: repeat(2, calc(50% - (1 * 30px) / 2));
    }  
}